import { useParams } from "react-router-dom";
import { StyleSheet, css } from "aphrodite-jss";
import { Canvas } from "@react-three/fiber";
import { useLoader } from '@react-three/fiber'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { Suspense, useState } from "react";
import ThreeDProductView from "./ThreeDProductView";
import { Environment, OrbitControls } from "@react-three/drei";

const Product = () => {
	const params = useParams();
	const [enable3d, setEnable3d] = useState(false)
	// const gltf = useLoader(GLTFLoader, "./3d-models/MaterialsVariantsShoe/MaterialsVariantsShoe.gltf");
	console.log("params", params, params.prodName);

	const styles = StyleSheet.create({
		"product-info": {
			display: "flex",
			padding: 20,
			gap: "20px",
		},
	});

	const allProductsData = [
		{
			name: "Divine Casa 120 GSM",
			price: 608,
			featured_image: "Divine Casa 120 GSM.webp",
			about_item:
				"Divine Casa Single Size Skin Friendly Polyester Dohar | Best Fitted for Adults",
			brand: "Divine Casa",
			brand_website: "https://divinecasa.com",
			preset: "sunset",
			model: "MaterialsVariantsShoe/MaterialsVariantsShoe.gltf",
			scale: 15
		},
		{
			name: "Livpure Smart Microfiber",
			price: 1799,
			featured_image: "Livpure Smart Microfiber.webp",
			product_category: "bedsheets",
			about_item: "200 GSM Microfiber Filling : Perfect to use all year",
			brand: "Livpure Smart",
			brand_website: "https://livpure.com",
			preset: "forest",
			model: "SheenChair/SheenChair.gltf",
			scale: 5
		},
		{
			name: "Wolpin Wall Stickers",
			price: 199,
			featured_image: "Wolpin Wall Stickers.jpg",
			product_category: "decoration",
			about_item: "MATERIAL: PVC Vinyl ROLL SIZE: Large Size in cms: 45 x 500 cm [Need 5 Rolls to cover a 10 ft by 10 ft Wall Size.]",
			brand: "Wolpin",
			brand_website: "#",
			preset: "lobby",
			model: "ToyCar/ToyCar.gltf",
			scale: 60
		},
		{
			name: "Decals Design Wall Sticker",
			price: 231,
			featured_image: "Decals Design Wall Sticker.jpg",
			product_category: "decoration",
			about_item: "Material: PVC Vinyl Color: Multicolor Item Size: 50 cm * 70 cm Finished Size on Wall (W*H): 90 cm * 133 cm",
			brand: "Decals Designs",
			brand_website: "https://www.crdecalsdesigns.com/",
			preset: "night",
			model: "IridescentDishWithOlives/IridescentDishWithOlives.gltf",
			scale: 15
		},
	];

	// filtering specific product based on product url
	const singleProduct = allProductsData.filter(
		(product) => product.name === params.prodName
	);
	function toggle3dview() {
		setEnable3d(!enable3d);
	}
	console.log("singleProduct ", singleProduct);
	return singleProduct.length ? (
		<div className={css(styles["product-info"])}>
			<div className="product-feaured-view" style={{ maxWidth: "400px", width: "400px" }} >
				<Canvas style={{ display: enable3d ? "block" : "none", width: "400px" }}>
					<Suspense fallback={"Loading"}>
						<Environment
							background={true}
							// files="venice/venice_sunset_1k.hdr"
							// files="lebombo/lebombo_1k.hdr"
							// path="https://market-assets.fra1.cdn.digitaloceanspaces.com/market-assets/hdris/"
							resolution={256}
							preset={singleProduct[0].preset}
						/>
						<OrbitControls autoRotate enableZoom={false} />
						<ThreeDProductView model={singleProduct[0].model} scale={singleProduct[0].scale} />
					</Suspense>
				</Canvas>
				<img
					src={"/images/" + singleProduct[0].featured_image}
					alt="Featured"
					style={{ display: enable3d ? "none" : "block", width: "100%", objectFit: "scale-down" }}
				/>
			</div>
			<div>
				<h1> {params.prodName} </h1>
				{/* prettier-ignore */}
				<a href={singleProduct[0].brand_website} target="_blank" rel="noreferrer" >
					Brand: {singleProduct[0].brand}
				</a>
				<p>
					Price :
					{new Intl.NumberFormat("en-IN", {
						style: "currency",
						currency: "INR",
					}).format(singleProduct[0].price)}
				</p>
				<h2>About this item</h2>
				<p>{singleProduct[0].about_item}</p>
				<button onClick={toggle3dview} >{enable3d ? "Close 3d view" : "Display 3d view"}</button>
			</div>
		</div>
	) : (
		<div>
			<p>product data unavailable</p>
		</div>
	);
};

export default Product;
