import { StyleSheet, css } from "aphrodite-jss";

const styles = StyleSheet.create({
	app_header: {
		"background-color": "#000",
		padding: 10,
	},
});

const Header = () => {
	return (
		<header className={css(styles.app_header)}>
			<a href="/">
				<img src="/images/logo.png" alt="Amazon Logo" width={100} />
			</a>
		</header>
	);
};

export default Header;
