import { StyleSheet, css } from "aphrodite-jss";
import { Link } from "react-router-dom";

const styles = StyleSheet.create({
	app: {
		"background-color": "#eaeded",
	},
	app_main: {
		display: "flex",
		padding: 20,
		gap: "20px",
		"flex-wrap": "wrap",
		"@media only screen and (max-width: 425px)": {
			"flex-direction": "column",
		},
	},
	categories_section: {
		"background-color": "#fff",
		// width: "21.5%"
		padding: 15,
		flex: "1 1 max(160px, 23.5%)",
		"& h2": {
			"font-size": "1.3rem",
		},
	},
	categories_pages: {
		display: "flex",
		"flex-wrap": "wrap",
		gap: "15px",
	},
	sng_ctgry: {
		// width: "max(100px, 9.5vw)",
		flex: "1 1 max(100px, 9.5vw)",
		"& a": {
			display: "block",
			"text-decoration": "none",
			color: "#000",
		},
		"& img": {
			width: "100%",
		},
		"& p": {
			"font-size": ".75rem",
			"font-weight": "600",
			"margin-top": 0,
		},
	},
	// "@media"
});
console.log("styles ", styles);

