import { StyleSheet, css } from "aphrodite-jss";
import uuid from "react-uuid";
import { Link, useParams } from "react-router-dom";

const Category = () => {
	const params = useParams();
	console.log("params", params, params.catid);

	const styles = StyleSheet.create({
		"category-products": {
			display: "flex",
			gap: "20px",
			padding: 20,
			"flex-wrap": "wrap",
		},
		"single-product": {
			// "background-color": "#000"
			flex: "1 1 max(275px, 23%)",
			"box-shadow":
				"rgb(50 50 93 / 25%) 0px 50px 100px -20px, rgb(0 0 0 / 30%) 0px 30px 60px -30px",
			transition: "box-shadow .5s",
			"&:hover": {
				"box-shadow": "rgb(0 0 0 / 56%) 0px 22px 70px 4px",
			},
      "& a":{
        display: "block",
        "text-decoration": "none",
        color: "#000"
      },
			"& img": {
				width: "100%",
				"object-fit": "contain",
				height: "250px",
				background: "hsl(300deg 8% 97%)",
			},
		},
		"product-meta": {
			padding: 30,
			"& p": {
				margin: 10,
			},
		},
	});
	const allProductsData = [
		{
			name: "Divine Casa 120 GSM",
			price: 608,
			featured_image: "Divine Casa 120 GSM.webp",
			product_category: "bedsheets",
		},
		{
			name: "Livpure Smart Microfiber",
			price: 1799,
			featured_image: "Livpure Smart Microfiber.webp",
			product_category: "bedsheets",
		},
		{
			name: "Amazon Brand - Solimo Microfibre",
			price: 1199,
			featured_image: "Amazon Brand - Solimo Microfibre.webp",
			product_category: "bedsheets",
		},
		{
			name: "Tulaka Luxury Comforter",
			price: 2499,
			featured_image: "Tulaka Luxury Comforter.webp",
			product_category: "bedsheets",
		},
		{
			name: "AmazonBasics Easy-Wash",
			price: 1479,
			featured_image: "AmazonBasics Easy-Wash.webp",
			product_category: "bedsheets",
		},
		{
			name: "STARNSTYLE 350TC Microfiber",
			price: 1699,
			featured_image: "STARNSTYLE 350TC Microfiber.webp",
			product_category: "bedsheets",
		},
		{
			name: "Cloth Fusion Pacifier Microfibre",
			price: 1799,
			featured_image: "Cloth Fusion Pacifier Microfibre.webp",
			product_category: "bedsheets",
		},
		{
			name: "Divine Casa Polyester",
			price: 1048,
			featured_image: "Divine Casa Polyester.webp",
			product_category: "bedsheets",
		},
		{
			name: "Wolpin Wall Stickers",
			price: 199,
			featured_image: "Wolpin Wall Stickers.jpg",
			product_category: "decoration",
		},
		{
			name: "Decals Design Wall Sticker",
			price: 231,
			featured_image: "Decals Design Wall Sticker.jpg",
			product_category: "decoration",
		},
		{
			name: "Ajanta Abstract Metal Quartz",
			price: 489,
			featured_image: "Ajanta Abstract Metal Quartz.jpg",
			product_category: "decoration",
		},
		{
			name: "Decals Design Flowers Branch",
			price: 140,
			featured_image: "Decals Design Flowers Branch.jpg",
			product_category: "decoration",
		},
		{
			name: "Ajanta Plastic Abstract Theme",
			price: 325,
			featured_image: "Ajanta Plastic Abstract Theme.jpg",
			product_category: "decoration",
		},
		{
			name: "Basement Bazaar Glass Sarfaraaz",
			price: 340,
			featured_image: "Basement Bazaar Glass Sarfaraaz.jpg",
			product_category: "decoration",
		},
		{
			name: "Wolpin Wall Stickers Wallpaper",
			price: 199,
			featured_image: "Wolpin Wall Stickers Wallpaper.jpg",
			product_category: "decoration",
		},
		{
			name: "Decals Design Tree with Birds and Cages",
			price: 150,
			featured_image: "Decals Design Tree with Birds and Cages.jpg",
			product_category: "decoration",
		},
	];

	//filtering specific products based on category url
	const catSpecificProds = allProductsData.filter(
		(product) => product.product_category === params.catid
	);

	return (
		<div className={css(styles["category-products"])}>
			{catSpecificProds.map((product) => {
				return (
					<div key={uuid()} className={css(styles["single-product"])}>
						<Link to={`/product/${product.name}`}>
							<img
								src={"/images/" + product.featured_image}
								alt={product.name}
							/>
							<div className={css(styles["product-meta"])}>
								<p> {product.name} </p>
								<p>
									{new Intl.NumberFormat("en-IN", {
										style: "currency",
										currency: "INR",
									}).format(product.price)}
								</p>
							</div>
						</Link>
					</div>
				);
			})}
		</div>
	);
};

export default Category;
