import { useGLTF } from "@react-three/drei";

const ThreeDProductView = ({model, scale}) => {

  // const gltf = useGLTF("/3d-models/AnimatedCube/AnimatedCube.gltf");
  const gltf = useGLTF(`/3d-models/${model}`);

  return (
    <primitive object={gltf.scene} position={[1, -1, 0]} scale={scale} color={"red"} />
  )
}

export default ThreeDProductView;